"use strict";
const styled_components_1 = require("styled-components");
const colors_1 = require("../assets/colors");
const fonts = require("../assets/fonts");
exports.borderedInput = (props) => {
    return styled_components_1.css `
    padding: 10px;
    border: 2px solid ${props.error ? colors_1.default.redRegular : colors_1.default.grayRegular};
    box-shadow: none;
    font-size: 17px;
    transition: border-color .25s;
    margin-right: 10px;
    resize: none;

    ${fonts.secondary.style}

    &:focus {
      border-color: ${props.error ? colors_1.default.redRegular : colors_1.default.purpleRegular};
      outline: 0;
    }

    &:disabled {
      border: 2px dotted ${colors_1.default.grayRegular};
    }
  `;
};
